@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@400;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
/* Default Font Styles
______________________*/
body,
input,
select,
textarea {
  font-family: 'Montserrat Alternates', sans-serif;
  font-size: 14px;
  color: #aba7d5;
  line-height: 1.3;
}
.body {
  background: #0b0516;
  min-height: 100vh;
}

.banner {
  background: url(../images/website_banner.png) center top;
}

.page-container {
  /*font-family: 'Montserrat Alternates', sans-serif;*/
  font-family: 'Open Sans', sans-serif;
  width: 80%;
  margin: 0 auto;
  padding: 20px 0px;
}

.heading-color {
  font-family: 'Montserrat Alternates', sans-serif;
  color: white;
  font-weight: 700;
  padding-top: 15px;
  padding-bottom: 25px;
}

.text-color {
  color: white;
}

.first-child {
  padding-left: 20px;
}

table tr th {
  border: 1px solid #ddd;
  padding: 8px;
}

/* Headings
______________________*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  margin: 0 0 20px;
}

/* Links
______________________*/

@media (min-width: 1600px) {
  body .container {
    max-width: 1600px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #7c7c7c;
}

a:hover {
  color: #7c7c7c;
  text-decoration: none;
}
.text-underline {
  text-decoration: underline;
}

/* p, blockquote, address
______________________*/
p {
  line-height: 1.6;
  margin: 0 0 20px;
}

.banner-outer {
  min-height: calc(100vh - 40px);
  position: relative;
  overflow: hidden;
  margin-top: -65px;

  background: #0b0516;
  background: rgba(23, 3, 55, 100%);
  background-image: linear-gradient(#0b0516, #1c033d);
}
.banner-outer:before {
  content: ' ';
  width: 600px;
  height: 600px;
  background: rgba(255, 255, 255, 0.6);
  opacity: 6%;
  position: absolute;
  left: -100px;
  bottom: -200px;
  border-radius: 50%;
}
.banner-outer:after {
  content: ' ';
  width: 550px;
  height: 550px;
  background: rgba(255, 255, 255, 0.6);
  opacity: 6%;
  position: absolute;
  right: -100px;
  bottom: -200px;
  border-radius: 50%;
}
.banner-bot {
  height: 100px;
  background: #0b0516;
  background: url(../images/strip.png);
}
.banner-section {
  background: url(../images//banner.png) center top no-repeat;
  background-size: 100% auto;
  min-height: 890px;
  max-width: 1500px;
  margin: 0 auto;
  position: relative;
  font-family: 'Montserrat Alternates', sans-serif;
}
/* .banner-section:before{
  content: " ";
  width: 400px;
  height: 400px;
  background: rgba(255,255,255, 0.6);
  opacity: 6%;
  position: absolute;
  left: -92px;
  top: -177px;
  border-radius: 50%;
} */
.banner-content {
  padding: 230px 0 0 120px;
}
.logo {
  position: relative;
  margin: 0 0 40px;
}
.logo img {
  width: 180px;
}
.banner-content h1 {
  color: #fff;
  font-weight: bold;
  font-size: 80px;
  line-height: 1;
  margin: 0 0 10px;
}
.banner-icons {
  position: absolute;
  left: 120px;
  bottom: 135px;
}
.banner-icons ul,
.banner-icons li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.banner-icons ul {
  display: flex;
  flex-wrap: wrap;
}
.banner-icons li {
  margin: 0 15px 15px 0;
}
.banner-icons li img {
  max-width: 160px;
}
.friends {
  display: block;
  margin: 0 0 30px;
}
.banner-mobile {
  display: none;
}

/* footer
______________________*/
#footer-wrap {
  background: #000000;
  font-family: 'Montserrat Alternates', sans-serif;
}
#footer {
  padding: 25px 80px;
}
#footer ul,
#footer li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer-section-wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.footer-logo {
  width: 150px;
}
.footer-nav {
  width: calc(100% - 200px);
  margin-left: auto;
}
.footer-nav a {
  color: #aba7d5 !important;
  /* color: rgba(171,167,213, 0.5) !important; */
}
#footer .footer-nav ul {
  display: flex;
  flex-wrap: wrap;
}
#footer .footer-nav li {
  margin: 5px 20px 5px 0;
}
#footer .footer-nav li:last-child {
  margin-right: 0;
}
#footer .footer-icons ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
#footer .footer-icons ul li {
  margin: 0 10px 10px 0;
}
#footer .footer-icons li img {
}

.cl {
  clear: both;
  font-size: 0;
  height: 0;
}

.clearfix:after {
  clear: both;
  content: ' ';
  display: block;
  font-size: 0;
  line-height: 0;
  visibility: hidden;
  width: 0;
  height: 0;
}

@media (max-width: 1600px) {
  .banner-section {
    min-height: 600px;
  }
  /* .banner-section:before{
    width: 300px;
    height: 300px;
    left: -72px;
    top: -133px;
  } */
  .banner-content {
    padding: 185px 0 0 90px;
  }
  .logo img {
    width: 150px;
  }
  .banner-content h1 {
    font-size: 60px;
  }
  .friends img {
    width: 200px;
  }
  .banner-icons {
    bottom: 35px;
    left: 90px;
  }
  .banner-icons li img {
    width: 120px;
  }
  #footer {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media (max-width: 1200px) {
  .banner-outer:before {
    width: 550px;
    height: 550px;
  }
  .banner-outer:after {
    width: 500px;
    height: 500px;
  }
  .banner-section {
    min-height: 590px;
  }
  .banner-section:before {
    width: 250px;
    height: 250px;
    left: -57px;
    top: -110px;
  }
  .banner-content {
    padding: 165px 0 0 90px;
  }
  .logo img {
    width: 120px;
  }
  .banner-content h1 {
    font-size: 50px;
  }
  .friends img {
    width: 150px;
  }
  .banner-icons {
    bottom: 30px;
    left: 90px;
  }
  .banner-icons li img {
    width: 100px;
  }
  .footer-logo {
    width: 120px;
  }
  .footer-nav {
    width: calc(100% - 170px);
  }
}
@media (max-width: 991px) {
  .banner-outer:before {
    width: 450px;
    height: 450px;
  }
  .banner-outer:after {
    width: 400px;
    height: 400px;
  }
  .banner-section {
    min-height: 390px;
  }
  .banner-section:before {
    width: 200px;
    height: 200px;
    left: -56px;
    top: -84px;
  }
  .banner-content {
    padding: 125px 0 0 60px;
  }
  .logo {
    margin: 0 0 20px;
  }
  .logo img {
    width: 80px;
  }
  .banner-content h1 {
    font-size: 36px;
  }
  .friends img {
    width: 100px;
  }
  .banner-icons {
    bottom: 30px;
    left: 50px;
  }
  .banner-icons li img {
    width: 75px;
  }
  #footer {
    font-size: 12px;
    padding-left: 0;
    padding-right: 0;
  }
  #footer .footer-nav li {
    margin-right: 10px;
  }
  .footer-logo {
    width: 100px;
  }
  .footer-nav {
    width: calc(100% - 130px);
  }
}
@media (max-width: 767px) {
  .banner-outer {
    margin: -20px 0 0;
    position: relative;
  }
  /* .banner-outer,
  .banner-content{
    min-height: calc(100vh - 84px);
  } */
  .banner-outer {
    min-height: calc(100vh - 84px);
  }
  .banner-content {
    padding: 40px 0 0 5px;
    position: static;
  }
  /* .banner-content:before{
    content: " ";
    background: url(../images/mobiles.png) center 70% no-repeat;
    background-size: 100% auto;
    position: absolute;
    bottom: 0;
    left: 40px;
    width: 255px;
    height: 390px;
  } */
  .banner-section {
    min-height: 275px;
    max-width: 375px;
    margin: 0 auto;
    background: none;
  }
  .banner-section:before {
    display: none;
  }
  .banner-outer:before {
    left: -400px;
    bottom: -100px;
    width: 600px;
    height: 600px;
  }
  .banner-outer:after {
    width: 400px;
    height: 400px;
    right: -80px;
    bottom: auto;
    top: -200px;
  }
  .friends {
    margin: 0 0 10px;
  }
  .logo {
    margin: 0 0 10px;
  }
  .logo img {
    width: 110px;
  }
  .banner-content h1 {
    font-size: 40px;
    margin: 0 0 5px;
  }
  .friends img {
    width: 160px;
  }
  .banner-icons {
    position: static;
    bottom: 20px;
    left: 20px;
  }
  /* .banner-icons ul{
      display: block;
    } */
  .banner-icons li img {
    width: 130px;
  }
  .banner-bot {
    display: none;
  }
  .banner-mobile {
    display: block;
    background-image: linear-gradient(#190238, #5c6fd5, #22f1f7);
    border-radius: 25px;
    margin: 0 25px 45px;
  }
  .mobile-photo-wrap {
    margin: 0 -55px;
    position: relative;
    bottom: -40px;
  }
  .mobile-photo-wrap img {
    display: block;
    width: 100%;
  }
  #footer {
    padding: 25px 10px 20px;
  }
  .footer-logo {
    width: 80px;
    margin: 0 0 10px;
  }
  .footer-nav {
    width: 100%;
  }
  #footer .footer-icons ul {
    justify-content: flex-start;
    padding: 15px 0 0;
  }
}
@media (max-width: 330px) {
  .banner-content {
    padding: 50px 0 0 20px;
  }
}
